.custom-select {
  position: relative;
  width: 100%;
  font-family: "Inter", sans-serif;
}

/* Верхній блок (заголовок селекту) */
.select-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #fff;
  padding: 0 12px;
  height: 40px;
  cursor: pointer;
  transition: border-color 0.2s;
}

.select-header:hover {
  border-color: #D31F37;
}

/* Іконка стрілки */
.arrow {
  transition: transform 0.2s ease;
}

.arrow.open {
  transform: rotate(180deg);
}

/* Список опцій */
.select-options {
  position: absolute;
  top: calc(100% + 4px);
  left: 0;
  width: 100%;
  background: #fff;
  border: 1px solid #fff;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  z-index: 10;
  overflow: hidden;
  animation: fadeIn 0.15s ease;
}

/* Окремий елемент списку */
.option {
  padding: 8px 12px;
  cursor: pointer;
  transition: background 0.2s, color 0.2s;
}

/* 🔥 Hover ефект */
.option:hover {
  background: #D31F37;
  color: #fff;
}

/* Вибрана опція */
.option.selected {
  background: #D31F37;
  color: #fff;
  font-weight: 600;
}

/* Анімація */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-2px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
